<template>
  <div class="p-0 contain" style="">
    <!-- <div class="cover"></div> -->

    <div v-if="edit === true" class="text-center title">تعديل معلومات الوحدة</div>
    <div v-if="edit === false" class="text-center title">اضافة وحدة جديدة</div>
    <section class="cont">
      <div class="formm">
        <div class="fields row">
          <div class="col-md-6">
            <div class="field">
              <label>اسم الوحدة</label>
              <input
                id="year"
                class="form-control"
                type="text"
                v-model="name"
                placeholder="اسم الوحدة"
              />
            </div>
          </div>
          <div class="col-md-6">
            <div class="field">
              <label>رقم الوحدة</label>
              <input
                v-model.number="number"
                class="form-control"
                type="number"
                min="1"
                placeholder="رقم الوحدة"
              />
            </div>
          </div>
          <div class="col-md-6">
            <div class="field">
              <label>سعر الوحدة</label>
              <input
                v-model.number="price"
                class="form-control"
                type="number"
                min="0"
                placeholder="سعر الدورة"
              />
            </div>
          </div>
          <div class="col-md-6">
            <div class="field">
              <label>المرحلة الدراسية</label>
              <select class="form-control" v-model="stage">
                <option
                  v-for="stage in $store.getters.stages"
                  :key="stage.value"
                  :value="stage.value"
                >
                  {{ stage.text }}
                </option>
              </select>
            </div>
          </div>
          <!------------------------->
          <!------------------------->
          <div class="center"></div>
        </div>

        <div v-if="loading" class="process">
          <loading color="#fff" />
        </div>

        <div class="upload">
          <button style="margin-top: 20px" @click="addSection" class="btn btn-danger">
            اضافة الوحدة
          </button>
        </div>
      </div>
    </section>
    <!--------------------------------------------------------->
    <app-iframe :src="$store.getters.expVideos.units" />
    <!--------------------------------------------------------->
  </div>
</template>

<script>
import axios from "axios";
import Loading from "../../components/loading.vue";
export default {
  components: {
    Loading,
  },
  mounted() {
    const params = this.$route.params;
    if (params.id) {
      axios.get("/getSection/" + params.id).then(async (res) => {
        this.name = res.data.section.name;
        this.number = res.data.section.number;
        this.price = res.data.section.price;
        this.stage = res.data.section.stage;
        this.image = res.data.section.image;
        // this.orderedTasks = res.data.section.orderedTasks;
        // this.imagePreview = res.data.section.image;
        this.id = res.data.section._id;
        this.edit = true;
      });
    }
  },
  data() {
    return {
      edit: false,
      name: null,
      number: 1,
      price: 0,
      stage: "6",
      image: null,
      // orderedTasks: false,
      id: null,
      loading: false,
      message: null,
    };
  },
  watch: {
    message(val) {
      setTimeout(() => {
        this.message = null;
      }, 3000);
    },
  },
  methods: {
    addSection() {
      if (this.name && this.number && typeof this.price == "number") {
        this.loading = true;
        const method = this.edit ? "/editSection/" + this.id : "/addSection";
        axios
          .post(method, {
            name: this.name,
            number: this.number,
            price: this.price,
            stage: this.stage,
            image: this.image,
          })
          .then((res) => {
            this.loading = false;
            if (res.data.error)
              return this.$store.dispatch("writemessage", res.data.error);
            this.$store.dispatch("writemessage", "تم أضافة الدورة بنجاح");
            this.$store.dispatch("getSections");
          });
      } else {
        alert("كمل بقيت البيانات وأرفع الفيديو");
      }
    },
  },
};
</script>

