<template>
  <div class="container">
    <h3 class="heading">الوحدات ({{ sections.length }})</h3>
    <br />
    <div class="row" dir="rtl">
      <div class="col-md-4">
        <label>اختر السنة الدراسية</label>
        <select class="form-control" v-model="stage">
          <option
            v-for="stage in $store.getters.stages"
            :key="stage.value"
            :value="stage.value"
          >
            {{ stage.text }}
          </option>
        </select>
      </div>
    </div>
    <!------------------------------------------------->
    <!-----------["title", "items", "loading", "colsTitles", "colsTypes", "btns", "color"],--------------->

    <div class="">
      <app-table
        title=""
        :items="sections"
        :cols="['number', 'name', 'stage', 'price', 'orderedTasks']"
        :colsNames="['رقم', 'العنوان', 'المرحلة', 'السعر', 'واجبات مرتبة']"
        :loading="loading"
        color="#222f22"
        :btns="[
          {
            icon: 'pencil',
            method: (section) => $router.push('editSection/' + section._id),
          },
          { icon: 'trash', method: deleteSection },
          {
            icon: 'list',
            method: (sec) => $router.push(`/dashboard/videos/${sec.number}/${sec.stage}`),
          },
        ]"
      />
    </div>
    <br />
    <hr />
    <add-section />
    <!--------------------------------------------------------->
    <app-iframe :src="$store.getters.expVideos.units" />
    <!--------------------------------------------------------->
  </div>
</template>
<script>
import axios from "axios";
import AddSection from "./add-lessons-section.vue";
export default {
  components: {
    AddSection,
  },
  created() {
    this.stage = "6";
  },
  data() {
    return {
      stage: "",
      loading: false,
    };
  },
  computed: {
    sections() {
      return this.$store.getters.sections.filter((sec) => sec.stage == this.stage);
    },
  },
  methods: {
    deleteSection(section) {
      const id = section._id;
      const confirmed = confirm("مسح الوحدة نهائيا ؟");
      if (!confirmed) return;
      axios.delete("/deleteSection/" + id).then((res) => {
        if (res.data.done) this.$store.dispatch("getSections");
      });
    },
  },
};
</script>
